html,
body {
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

figure {
  margin: 0 0 5rem;
  padding: 1rem;
}

section {
  margin-bottom: 5rem;
  margin-top: 5rem;
}

.container {
  box-sizing: border-box;
  margin: 0 auto;
  padding: 0 2vw;
  width: 100vw;
}
#credits {
  margin-bottom: 2rem;
}
[id^=calculation-steps--] {
  counter-reset: section;
  display: flex;
  flex-wrap: wrap;
}
[id^=calculation-steps--] > div {
  padding: 1.5rem;
  position: relative;
}
[id^=calculation-steps--] > div::after {
  bottom: 0.5em;
  counter-increment: section;
  content: "" counter(section) "";
  opacity: 0.2;
  position: absolute;
  right: 0.5em;
}
[id^=calculation-steps--] p {
  margin: 0 0 0.5rem;
}

.arithmatic {
  display: inline-block;
  text-align: right;
}
.arithmatic hr {
  border: none;
  display: block;
  height: 2px;
  margin: 0.5rem 0;
  padding: 0;
  width: 100%;
}
.arithmatic .solution {
  border: 2px dashed;
  margin-right: -0.25rem;
  padding: 0 0.25rem;
}

.recommendations {
  margin-top: 1rem;
}
.recommendations p {
  margin-bottom: 0.5rem;
}
.recommendations ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.recommendations ul li {
  margin-bottom: 0.5rem;
}
.recommendations ul li:last-child {
  margin-bottom: 0;
}

@media screen and (min-width: 768px) {
  .container {
    width: 90vw;
  }
}
@media screen and (min-width: 992px) {
  .container {
    width: 75vw;
  }
}
body,
html {
  font-family: "Surveyor SSm A", "Surveyor SSm B", serif;
  font-style: normal;
  font-weight: 400;
}

body {
  font-size: 2rem;
}

figcaption {
  font-size: 1rem;
  font-style: italic;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Tungsten Narrow A", "Tungsten Narrow B", sans-serif;
  font-weight: 700;
  letter-spacing: 5%;
  text-transform: uppercase;
}

h1 {
  border-bottom: 1px solid #001c0e;
  border-top: 1px solid #001c0e;
  font-size: 200%;
  line-height: 1;
  margin: 1rem 0;
  padding: 1rem 0;
  text-align: center;
  text-transform: uppercase;
}

h2 {
  margin: 0 0 1rem;
  padding: 0;
}

p,
li {
  font-size: 1.25rem;
  line-height: 1.4;
  margin-bottom: 0.5rem;
  max-width: 75ch;
}

p {
  margin: 0 0 2rem;
  padding: 0;
}
p.callout {
  font-size: 120%;
}
p.callout:last-of-type {
  margin-bottom: 2rem;
}

small,
.small {
  font-size: 75%;
  font-weight: 400;
}

footer {
  font-family: "Numbers Claimcheck A", "Numbers Claimcheck B", monospace;
}

input#kapInput {
  font-family: "Numbers Claimcheck A", "Numbers Claimcheck B", monospace;
}

[id^=error--] {
  border: 1px solid;
  display: inline-block;
  font-family: "Tungsten Narrow A", "Tungsten Narrow B", sans-serif;
  font-weight: 400;
  font-size: 1.75rem;
  letter-spacing: 5%;
  margin-bottom: 0.5rem;
  padding: 0.5rem 1rem;
  text-transform: uppercase;
}

[id^=calculation-steps--] > div::after {
  font-family: "Numbers Claimcheck A", "Numbers Claimcheck B", monospace;
  font-size: 4rem;
}

.arithmatic {
  font-family: "Numbers Claimcheck A", "Numbers Claimcheck B", monospace;
  font-size: 80%;
  text-align: right;
}

.numbers {
  font-family: "Numbers Claimcheck A", "Numbers Claimcheck B", monospace;
}

.recommendations ul li {
  font-size: 0.9rem;
  line-height: 1;
}

@media screen and (min-width: 768px) {
  h1 {
    font-size: 300%;
  }
}
@media screen and (min-width: 992px) {
  h1 {
    font-size: 325%;
  }
}
ol {
  counter-reset: my-awesome-counter;
  list-style: none;
  margin: 3rem 0;
  padding: 0;
}

ol li {
  counter-increment: my-awesome-counter;
  margin-bottom: 1.75rem;
}

ol li::before {
  color: #de5617;
  content: counter(my-awesome-counter) ". ";
  font-family: "Numbers Claimcheck A", "Numbers Claimcheck B", monospace;
  font-size: 125%;
}

input {
  box-sizing: border-box;
  display: block;
  width: 100%;
}

#button--start-calculation {
  background: #de5617;
  border: none;
  color: #faf9f3;
  display: block;
  font-family: "Tungsten Narrow A", "Tungsten Narrow B", sans-serif;
  font-size: 150%;
  font-weight: 400;
  letter-spacing: 5%;
  line-height: 1;
  margin-top: 1rem;
  padding: 0.5rem 5rem;
  text-transform: uppercase;
}
#button--start-calculation:hover {
  cursor: pointer;
}

.hide {
  display: none;
}

body,
html {
  background: #faf9f3;
  color: #001c0e;
}

body {
  background: transparent;
}

a {
  color: #de5617;
  text-decoration: underline;
}
em {
  font-style: italic;
}

h1 {
  color: #001c0e;
}

#calculation-results h2 {
  border-bottom: 1px solid #001c0e;
}

strong {
  font-weight: 700;
}

[id^=error--] {
  border-color: #de5617;
  color: #001c0e;
}

figure {
  background: rgb(253.75, 253.5, 252);
  background-image: linear-gradient(rgba(0, 28, 14, 0.05) 1px, transparent 1px), linear-gradient(90deg, rgba(0, 28, 14, 0.05) 1px, transparent 1px);
  background-position: top left;
  background-size: 1rem 1rem;
  box-shadow: rgba(0, 28, 14, 0.2) 0px 1px 2px 0px, rgba(0, 28, 14, 0.05) 0px 1px 3px 1px;
}

[id^=calculation-steps--] {
  background: rgb(253.75, 253.5, 252);
  background-image: linear-gradient(rgba(0, 28, 14, 0.05) 1px, transparent 1px), linear-gradient(90deg, rgba(0, 28, 14, 0.05) 1px, transparent 1px);
  background-position: top left;
  background-size: 1rem 1rem;
  border-radius: 2px;
  box-shadow: rgba(0, 28, 14, 0.2) 0px 1px 2px 0px, rgba(0, 28, 14, 0.05) 0px 1px 3px 1px;
}
figure [id^=calculation-steps--] {
  background: none;
  border-radius: 0;
  box-shadow: none;
}
[id^=calculation-steps--] > div::after {
  color: #de5617;
}
[id^=calculation-steps--] .arithmatic {
  color: #de5617;
}

.arithmatic hr {
  background: #de5617;
}
.arithmatic .solution {
  border-color: #de5617;
}

footer {
  padding: 1rem 0;
}
footer .link {
  display: flex;
  align-items: center;
}
footer .link img {
  display: inline-block;
  height: 1rem;
  margin: 0 0.75rem 0 0;
}
footer .link p {
  margin: 0;
}

.highlight {
  color: #de5617;
}

